import "./product.scss";
import React, { useState, useEffect } from "react";
import Navbar from "../../components/navbar/navbar";
import MainData from "../../asset/data/mainData";
import Footer from "../../components/footer/footer";
import { useLocation } from "react-router-dom";

function Product() {
  const [data, setData] = useState([]);
  const [image, setImage] = useState('');
  const location = useLocation();
  const id = location.pathname?.split("product/")[1];
  useEffect(() => {
    window.scrollTo(0, 0);
    fetchData();
  }, []);

  const fetchData =  () => {
    setData(MainData[parseInt(id) - 1]);
    // console.log("ahihii", id);
    setImage(MainData[parseInt(id) - 1].image);

  };
  const onChangeImage =(field) =>{
    setImage(field);
  }
  return (
    <div className="container">
      <div className="top2">
        <Navbar />
      </div>
      <div className="content">
        <div className="content-product">
          <div className="content-img">
            <img src={image} alt="" />
            <div className="item">
              <img src={data.item1} alt="" onClick={() => onChangeImage(data.item1)}/>
              <img src={data.item2} alt="" onClick={() => onChangeImage(data.item2)}/>
              <img src={data.item3} alt="" onClick={() => onChangeImage(data.item3)} />
              <img src={data.item4} alt="" onClick={() => onChangeImage(data.item4)}/>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}
export default Product;
